@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.contact_page_content {
  padding: 0 4vmin;
}

.wrap_app {
  margin: 4vmin auto;
  font-family: 'Lato', sans-serif;
  width: 330px;
  height: 480px;
  background-color: black;
  border: solid 1px bisque;
  border-radius: 5px;
  overflow: scroll;

}

.wrap_app::-webkit-scrollbar {
  width: 0;
}

.wrap_search {
  padding: 12px;
  position: relative;
}

.search_panel {
  padding: 0 18px;
  background-color: #202124;
  border-top: solid 1px #202124;
  border-right: solid 1px #202124;
  border-left: solid 1px #202124;
  border-bottom: solid 1px bisque;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  height: 50px;
  width: 100%;
  color: darkgrey;
  font-size: 24px;
  position: -webkit-sticky;
  position: sticky;
  top: 12px;
  z-index: 999;
}

.search_list {
  max-height: 395px;
  margin: 0;
  padding-bottom: 3px;
  background-color: #202124;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border-right: solid 1px bisque;
  border-left: solid 1px bisque;
  border-bottom: solid 1px bisque;
  display: none;
  position: -webkit-sticky;
  position: sticky;
  top: 48px;
}

.text {
  margin: 0;
  font-size: 12px;
  text-align: center;
  color: #757575;
}

.search_panel:focus {
  border-top: solid 1px bisque;
  border-right: solid 1px bisque;
  border-left: solid 1px bisque;
  border-bottom: none;
}

.search_panel:focus+.search_list {
  display: block;
}

.wrap_contact:hover {
  background-color: rgb(104, 102, 102);
  ;
  cursor: pointer;
}

.wrap_data_contact {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: space-around;
  justify-content: space-around;
}

.wrap_contact {
  margin: 15px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  grid-gap: 10px;
  grid-gap: 10px;
  gap: 10px;
}

.contact_icon {
  margin: 5px 15px;
}

.contact_data {
  margin: 3px 8px 3px 10px;
}
.gallery_page_content {
  -webkit-justify-content: center;
          justify-content: center;
  margin: 3vmin auto;
  max-width: 100vmin
}

.gallery_page_content img {
  min-width: 15vmin;
  max-width: 40vmin;
  margin: 1vmin;
  object-fit: cover;
  border: 1px solid bisque;
  border-radius: 5px
}
.post_box {
  margin: 5vmin;
  background-color: #15202B;
  color: #fff;
  border: solid 1px bisque;
  border-radius: 5px;
}

.author_img {
  height: 8vmin;
  border-radius: 50%;
  margin: 2vmin;
}

.wrap_content {
  grid-gap: 1vmin;
  gap: 1vmin;
  margin: 2vmin 2vmin 2vmin 0;
}

.head_info {
  -webkit-align-items: center;
          align-items: center;
}

.author_nick,
.date_post {
  color: #8899A5;
  position: relative;
  margin: 0 1vmin;
}

.svg_verify {
  margin: 0 4vmin 0 1vmin;
  height: 2vmin;
}

.date_post::before {
  content: "\00B7";
  position: absolute;
  left: -1.2vmin;
}

.action_dropdown {
  margin-left: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.text_content {
  margin: 0 0 1vmin 0;
}

.img_media {
  max-width: 60vmin;
  min-width: 15vmin;
  object-fit: cover;
  border-radius: 5px;
}

.wrap_buttons {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.comment_btn,
.retweet_btn,
.heart_btn,
.send_btn {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  border: none;
  background-color: transparent;
  /* font-size: inherit; */
  color: #8899A5;
  /* position: relative; */
  height: 2vmin;
  width: 9vmin;
  cursor: pointer;
  font:unset;
}

.svg_icon,
.svg_dropdown {
  width: 4vmin;
  min-width: 15px;
  /* height: 4vmin; */
  margin-right: .5rem;
}

.count_btn {
  font-size: 1rem;
}

.comment_btn:hover .svg_icon {
  fill: #1DA1F2;
}

.retweet_btn:hover .svg_icon {
  fill: #17BF63;
}

.heart_btn:hover .svg_icon {
  fill: #E0245E;
}

.send_btn:hover .svg_icon,
.action_dropdown:hover .svg_dropdown {
  fill: #1DA1F2;
}
.home_page_content img {
  max-width: 100vmin;
  padding: 2vmin;
  object-fit: cover;
  width: 100%;
}
.nav_bar_wrapper  {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.nav_bar_logo_img {
  height: 5vmin;
  margin: 0 5vmin 0 0;
}

.nav_bar_links_list {
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 2vmin;
  gap: 2vmin;
  margin: 0;
  padding: 0;
}

.nav_bar_link_item {
  list-style-type: none;
}

.nav_bar_link_item a {
  color: bisque;
  text-decoration: none
}
.app_wrapper {
  margin: 5vmin auto;
  max-height: 80vh;
  min-height: 600px;
  background-color: #202124;
  border: 1px solid rgb(255, 227, 194);
  border-radius: 5px;
  overflow: scroll;
  position: relative;
}

.app_wrapper::-webkit-scrollbar {
  width: 0;
}

.app_header {
  width: 95%;
  min-width: 50vmin;
  margin: 0 auto;
  height: 9vmin;
  background-color: inherit;
  border-style: none;
  border-bottom: 1px solid bisque;
  padding: 1.5vmin;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}
* {
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
